/* Background Image & Full-Screen Layout */
.auth-container {
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    background-color: #0c0e25fd; /* Fallback background */
}


/* Transparent Login Box */
.auth-box {
    position: relative;
    background: rgba(79, 79, 89, 0.3); /* Transparent glass effect */
    backdrop-filter: blur(10px); /* Blur effect */
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    width: 350px;
    text-align: center;
    color: rgb(185, 184, 184);
    z-index: 1; /* Ensure it appears above the background image */
}

/* Login Box Headings */
.auth-box h2 {
    margin-bottom: 20px;
    font-size: 24px;
}

/* Input Fields */
.input-group {
    margin-bottom: 15px;
}

.input-group input,
.input-group select {
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    background-color: rgba(255, 255, 255, 0.2); /* Transparent input */
    color: rgb(0, 0, 0);
    outline: none;
}

.input-group input::placeholder,
.input-group select {
    color: rgb(0, 0, 0);
}

/* Login Button */
.btn-auth {
    width: 100%;
    padding: 10px;
    background-color: #ff5722;
    color: rgb(30, 25, 25);
    border: none;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    margin-top: 10px;
}

.btn-auth:hover {
    background-color: #e64a19;
}

/* Signup Link */
p {
    margin-top: 10px;
}

p a {
    color: #ff9800;
    text-decoration: none;
    font-weight: bold;
}

p a:hover {
    text-decoration: underline;
}
