/* Main container to center content */
.chef-dashboard-container {
  display: flex;
  height: 100vh;
}

/* Sidebar styles */
.sidebar {
  width: 20%;
  background-color: #1f2a48;
  color: white;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  height: 100%;
}

.sidebar h2 {
  margin-bottom: 20px;
  font-size: 24px;
}

.sidebar ul {
  list-style-type: none;
  padding: 0;
}

.sidebar li {
  margin: 10px 0;
  cursor: pointer;
}

/* Center content in the dashboard */
.dashboard-content {
  flex: 1;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center; /* Align content vertically */
  align-items: center; /* Center content horizontally */
}

h1 {
  font-size: 30px;
  margin-bottom: 30px;
}

h3 {
  margin-bottom: 20px;
  font-size: 24px;
}

/* Profile form styling */
.update-profile-form {
  width: 40%; /* Adjust this width as needed */
  background-color: #f4f6f9;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.update-profile-form label {
  font-size: 16px;
  margin: 10px 0 5px;
  display: block;
}

.update-profile-form input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.update-profile-form button {
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.update-profile-form button:hover {
  background-color: #0056b3;
}

/* Recipe list section */
.recipe-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

.recipe-card {
  background-color: #fff;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.recipe-image {
  max-width: 100%;
  height: auto;
  border-radius: 8px;
}

button {
  background-color: #ff5733;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

button:hover {
  background-color: #c1351d;
}
