/* General Styles */
.recipe-container {
  background-color: #0d1b2a;
  padding: 40px 20px;
  color: #ffffff;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  min-height: 100vh;
}

/* Search Bar */
.search-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 12px;
  margin-bottom: 30px;
  flex-wrap: wrap;
}

.search-bar label {
  font-size: 20px;
  color: #ffffff;
  font-weight: 600;
}

.search-bar input {
  padding: 12px 16px;
  width: 100%;
  max-width: 500px;
  border-radius: 10px;
  border: none;
  background-color: #2e3d4e;
  color: white;
  outline: none;
  font-size: 16px;
  transition: box-shadow 0.3s ease;
}

.search-bar input:focus {
  box-shadow: 0 0 0 2px #007bff;
}

/* Recipe Grid */
.recipe-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(380px, 1fr));
  gap: 25px;
}

/* Individual Recipe Card */
.recipe-card {
  display: flex;
  flex-direction: row;
  background-color: #ffffff;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
  transition: transform 0.25s ease, box-shadow 0.25s ease;
  height: 240px;
  padding: 0;
}

.recipe-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 28px rgba(0, 0, 0, 0.3);
}

/* Left Half: Image */
.card-img-container {
  flex: 1;
  height: 100%;
}

.card-img-top {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Right Half: Content */
.card-body {
  flex: 1;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #0d1b2a;
}

.card-title {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 6px;
}

.rating {
  color: #ffcc00;
  font-size: 14px;
  font-weight: 500;
}

.description {
  font-size: 14px;
  line-height: 1.5;
  color: #4b5c6b;
  margin-top: 6px;
}

/* Read More Button */
.read-more {
  align-self: flex-start;
  background: linear-gradient(to right, #007bff, #0056b3);
  color: white;
  border: none;
  padding: 10px 16px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s ease;
}

.read-more:hover {
  background: linear-gradient(to right, #0056b3, #003d80);
  transform: translateY(-2px);
}
