.chefs-section {
  text-align: center;
  padding: 40px;
  background-color: #0b1a34; /* Dark theme background */
  color: white;
}

.chefs-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px; /* Space between chef cards */
}

.chef-card {
  background-color: #1e2a47; /* Card background */
  border-radius: 12px;
  padding: 20px;
  width: 230px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease;
}

.chef-card:hover {
  transform: translateY(-8px);
}

.chef-image-container {
  width: 130px;
  height: 130px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 10px;
}

.chef-image {
  width: 110px;
  height: 110px;
  border-radius: 50%;
  object-fit: cover;
}

.chef-details h3 {
  font-size: 1.2rem;
  color: white;
  margin: 10px 0 5px;
}

.chef-details .role {
  font-size: 0.9rem;
  color: #cfcfcf;
}

.chef-details .rating {
  font-size: 1rem;
  color: gold;
  margin: 5px 0;
}

.chef-details .specialty {
  font-size: 1.1rem;
  font-weight: bold;
  color: white;
}
