/* General Styles */
.about-container {
  padding: 30px;
  max-width: 900px;
  margin: auto;
  background-color: #f9f9f9;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  text-align: center;
  font-family: "Arial", sans-serif;
}

/* Headings */
.about-container h1 {
  font-size: 28px;
  color: #2c3e50;
  margin-bottom: 10px;
}

.about-container h2 {
  font-size: 24px;
  color: #34495e;
  margin-top: 20px;
  border-bottom: 3px solid #2980b9;
  display: inline-block;
  padding-bottom: 5px;
}

/* Paragraphs */
.about-container p {
  font-size: 16px;
  color: #555;
  line-height: 1.8;
  text-align: justify;
  margin-bottom: 15px;
}

/* Feature List */
.about-list {
  list-style: none;
  padding: 0;
  margin: 20px 0;
}

.about-list li {
  font-size: 16px;
  color: #2c3e50;
  background: #ecf0f1;
  padding: 10px;
  border-radius: 8px;
  margin-bottom: 8px;
  display: flex;
  align-items: center;
  gap: 10px;
}

/* Team Section */
.team-section {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 20px;
  gap: 20px;
}

/* Team Members */
.team-member {
  background: white;
  padding: 15px;
  border-radius: 10px;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 200px;
}

.team-member:hover {
  transform: translateY(-8px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

.team-member img {
  border-radius: 50%;
  width: 120px;
  height: 120px;
  object-fit: cover;
  margin-bottom: 10px;
}

.team-member h3 {
  font-size: 18px;
  color: #2980b9;
  margin-bottom: 5px;
}

.team-member p {
  font-size: 14px;
  color: #7f8c8d;
  font-style: italic;
}

/* Responsive Design */
@media (max-width: 768px) {
  .team-section {
    flex-direction: column;
    align-items: center;
  }

  .about-list li {
    font-size: 14px;
    padding: 8px;
  }
}
