/* Home Page Styling */
.home-container {
  background-color: #0f1a2a;
  color: white;
  font-family: 'Arial', sans-serif;
  padding: 20px;
}

/* Hero Section */
.hero-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 50px 100px;
}

.hero-text {
  max-width: 50%;
}

.hero-text h1 {
  font-size: 36px;
  font-weight: bold;
}

.hero-text p {
  font-size: 18px;
  margin-top: 10px;
}

.hero-buttons {
  margin-top: 20px;
}

.btn-light, .btn-dark {
  padding: 12px 25px;
  border-radius: 25px;
  font-size: 16px;
  cursor: pointer;
  margin-right: 10px;
  border: none;
}

.btn-light {
  background: white;
  color: #0f1a2a;
}

.btn-dark {
  background: #ffcc00;
  color: black;
}

.hero-image img {
  width: 500px;
  border-radius: 20px;
}

/* Features Section */
.features-section {
  text-align: center;
  margin-top: 50px;
}

.feature-cards {
  display: flex;
  justify-content: center;
  gap: 40px;
  margin-top: 20px;
}

.feature-card {
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-radius: 20px;
  text-align: center;
  width: 300px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.feature-card:hover {
  transform: scale(1.05); /* Slightly enlarges the card */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* Adds shadow on hover */
}


.feature-card img {
  width: 80px;
  height: 80px;
}

.learn-more {
  display: block;
  margin-top: 10px;
  color: #ffcc00;
  text-decoration: none;
}

/* Community Section */
.community-section {
  text-align: center;
  margin-top: 80px;
  padding: 50px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 20px;
}

.community-image img {
  width: 300px;
  border-radius: 10px;
}

/* Expert Chefs */
.expert-chefs {
  text-align: center;
  margin-top: 80px;
  padding: 50px;
  background: black;
  border-radius: 20px;
}

.chef-card img {
  width: 200px;
  height: auto;
}
.learn-more-text {
  display: block;
  text-align: right;
  font-size: 18px;
  font-weight: bold;
  color: #f0f0f0;
  cursor: pointer;
  margin-top: 20px;
  padding-right: 20px; /* Adjust if needed */
  transition: color 0.3s ease-in-out;
}

.learn-more-text:hover {
  color: #f8f4e7; /* Slightly darker yellow on hover */
  text-decoration: underline;
}

/* Gallery Container */
.gallery {
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-radius: 20px;
  text-align: center;
  width: 300px;
}

/* Gallery Item */
.gallery-item {
  position: relative;
  overflow: hidden;
  border-radius: 8px;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.gallery-item img {
  width: 100%;
  height: auto;
  display: block;
  border-radius: 8px;
  object-fit: cover;
}

.gallery-item:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

/* Gallery Item Description */
.gallery-item .description {
  position: absolute;
  bottom: 10px;
  left: 10px;
  right: 10px;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  padding: 10px;
  border-radius: 4px;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.gallery-item:hover .description {
  opacity: 1;
}

/* Text Styles */
.gallery-item h3 {
  font-size: 18px;
  margin: 0;
  font-weight: bold;
  
}

.gallery-item p {
  font-size: 14px;
  margin-top: 5px;
  opacity: 0.8;
}
/* Reviews Section */
.reviews-section {
  

  align-items: center; /* Centers content vertically */
  margin-top: 50px; /* Adds space above the section */
  background: black; /* Sets background color to black */
  padding: 50px; /* Adds space inside the section */
  border-radius: 15px; /* Rounds the corners */
  color: rgb(255, 255, 255); /* Sets text color to white */
}

/* Centering the Contact Us and Reviews and Ratings headings */
h1 {
  text-align: center; /* Centers the text horizontally */
  margin: 20px 0; /* Adds space above and below the heading */
}








/* Review Box Styling */
.review-box {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #333; /* Dark gray background */
  border-radius: 25px;
  padding: 12px;
  width: 320px;
  margin: auto; /* Centers the review box */
}

.review-box input {
  flex-grow: 1;
  border: none;
  background: transparent;
  padding: 10px;
  font-size: 16px;
  color: white;
  outline: none;
}



/* Star Rating Section */
.star-rating {
  display: flex;
  justify-content: center;
  margin-top: 10px;
  font-size: 28px;
}

.star {
  cursor: pointer;
  transition: color 0.3s ease-in-out;
}

.star:hover,
.star:hover ~ .star {
  color: gold;
}

/* Contact Section inside Reviews Section */


.contact-section h2 {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
  text-align: center; /* Centers the heading */
}

.contact-section p {
  font-size: 16px;
  margin: 10px;
  text-align: center;
}

/* Social Icons Styling */
.social-icons {
  display: flex;
  justify-content: center; /* Centers the icons */
  gap: 12px;
  margin-top: 12px;
}

.social-icons img {
  width: 35px;
  height: 35px;
  transition: transform 0.3s ease-in-out;
}

.social-icons img:hover {
  transform: scale(1.1); /* Slight zoom effect on hover */
}


/* Footer */
.footer-section {
  text-align: center;
  margin-top: 50px;
  padding: 20px;
  background: #081220;
}
